import * as React from "react"

import Footer from "../components/footer"

import "../css/about.css";

import VerywiseLogo from "../images/verywiselogo.svg"
import TelegramLogo from "../images/telegramlogo.svg"
import Lares from "../images/twitter_lares.png"

const pageStyles = {
  color: "#232129",
  padding: "16px",
}
const headingStyles = {
  marginTop: 64,
  marginBottom: 64,
  maxWidth: 320,
  fontFamily: 'gothammedium',
  fontSize: '1.8rem'
}

const paragraphStyles = {
  marginBottom: 48,
}


const AboutPage = () => {
  return (
    <>
        <main style={pageStyles} className="about">
          <nav>
            <a href="/">Home</a>
            <a href="/about">About</a>
            <a href="https://t.me/renter_support_bot" target="_blank" rel="noopener">Telegram <img className="telegram-logo" src={TelegramLogo} height={22} width={22} alt="Telegram" /></a>
          </nav>
          <section>
            <h1>
              <a href="/"><img className="logo" src={VerywiseLogo} alt="Very Wise - Be Empowered" /></a>
              <span className="invisible">Very Wise - Be Empowered</span>
            </h1>
            <h2>Be Empowered</h2>
          </section>
          <h3 style={headingStyles}>About us</h3>
          <p style={paragraphStyles}>
            Hello and welcome to Lares the Very Wise artificial intelligence assistant for renters.<br/><br/>
            Lares is named after the ancient Roman household guardians who were believed to watch over households and ensure their well-being. <br/><br/>
            Today, Lares uses artificial intelligence to provide renters with an interactive and conversational experience. <br/><br/>
            Lares believes that everyone should have access to the same kind of protection and assistance that these guardians provided.<br/><br/>
            Be empowered with access to the knowledge and resources you need at any time of day or night, Lares is always here for you.<br/><br/>
            Our system is designed to understand your questions and provide you with accurate and helpful information.<br/><br/>
            With Lares by your side, you can rest assured that you have a reliable and knowledgeable companion to help you navigate the challenges of renting.<br/><br/><br/>
            NOTE: You should always seek legal advice from a qualified legal representative and treat the answers from Lares as information only and not advice.
          </p>
        </main>
      <Footer fixed={false} />
    </>
  )
}

export default AboutPage

export const Head = () => <>
  <title>Very Wise - Be Empowered</title>
  <meta name="description" content={`The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being. Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.`} />
  <meta name="image" content={`https://www.verywise.io/${Lares}`} />
  <meta name="twitter:card" content="summary_large_image" />
  <meta name="twitter:title" content="Very Wise - Be Empowered" />
  <meta name="twitter:url" content="https://www.verywise.io" />
  <meta name="twitter:description" content={`The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being. Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.`} />
  <meta name="twitter:image" content={`https://www.verywise.io/${Lares}`} />
  <meta name="twitter:creator" content="veriwise" />
</>

